import React from "react";

const Language = ({ languageName, percentage }) => {
  return (
    <>
      <p>{languageName}</p>
      <div className="w3-light-grey w3-round-xlarge">
        <div
          className="w3-round-xlarge w3-teal"
          style={{ height: "24px", width: `${percentage}%` }}
        ></div>
      </div>
    </>
  );
};

export default Language;
