import React from "react";
import { Helmet } from "react-helmet";
import Contact from "../components/contact";
import Footer from "../components/footer";
import Language from "../components/language";
import ProjectsSlide from "../components/projects";
import Skill from "../components/skill";
import avatar from "../assets/avatar.jpeg";
import AboutMe from "../components/aboutMe";
import "../assets/styles.css"

const Index = () => {
  return (
    <>
      <Helmet>
        {/* <link
          rel="stylesheet"
          href="https://www.w3schools.com/w3css/4/w3.css"
        /> */}
        <link
          rel="stylesheet"
          href="https://fonts.googleapis.com/css?family=Roboto"
        />
        <link
          rel="stylesheet"
          href="https://use.fontawesome.com/releases/v5.15.1/css/all.css"
        />
        <title>Ali Maldonado</title>
      </Helmet>
      <body className="w3-light-grey">
        <div
          className="w3-content w3-margin-top"
          style={{ maxWidth: "1400px" }}
        >
          <div className="w3-row-padding">
            <div className="w3-third">
              <div className="w3-white w3-text-grey w3-card-4">
                <div className="w3-display-container">
                  <img src={avatar} style={{ width: "100%" }} alt="Avatar" />
                  <div className="w3-display-bottomleft w3-container w3-text-black">
                    <h1>Ali Maldonado</h1>
                  </div>
                </div>
                <div className="w3-container">
                  <Contact />

                  <p className="w3-large">
                    <b>
                      <i className="fa fa-asterisk fa-fw w3-margin-right w3-text-teal"></i>
                      Skills
                    </b>
                  </p>
                  <Skill skillName="PHP + Symfony" percentage={80} />
                  <Skill
                    skillName="MySQL, SQL, Postgres; design and managment"
                    percentage={90}
                  />
                  <Skill
                    skillName="Python, scripting and APIs (Flask, FastAPI)"
                    percentage={70}
                  />
                  <Skill
                    skillName="Javascript, Vanilla, React and Node"
                    percentage={70}
                  />
                  <br />
                  <p className="w3-large w3-text-theme">
                    <b>
                      <i className="fa fa-globe fa-fw w3-margin-right w3-text-teal"></i>
                      Languages
                    </b>
                  </p>
                  <Language languageName="Spanish - Native" percentage={100} />
                  <Language
                    languageName="English - Proeficient"
                    percentage={70}
                  />
                  <br />
                </div>
              </div>
              <br />
            </div>
            <div className="w3-twothird">
              <AboutMe />
            </div>
            {/* Projects */}
            <ProjectsSlide />
          </div>
        </div>
        <Footer />
      </body>
    </>
  );
};

export default Index;
