import React from "react";
const Project = ({ title, subtitle, icon, url }) => {
  return (
    <>
      {" "}
      <h3 className="w3-text-grey w3-padding-16">
        <i
          className={`${icon} fa-fw w3-margin-right w3-xlarge w3-text-teal`}
        ></i>
        <a href={url} target="_blank" rel="noreferrer">
          {`${title}  `}
          <i class="fas fa-external-link-alt w3-small w3-text-teal"></i>
        </a>
      </h3>
      <div className="w3-container">
        <h5 className="w3-opacity">{subtitle}</h5>

        <p></p>
        <hr />
      </div>
    </>
  );
};

export default Project;
